import React from "react"
import FeaturedProject from "../Features/FeaturedProject"
import useAllProject from "../../hooks/use-all-project"

const ProjectFeed = () => {
  const allProject = useAllProject()

  return allProject.map((node, index) => {
    return <FeaturedProject key={index} feature={node} />
  })
}

export default ProjectFeed
